#contactForm {
 margin-top: 12%;
 text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: auto;
}

label {
  margin-bottom: 5px;
  align-self: flex-start;
}

input, textarea {
  padding: 5px;
  margin-bottom: 15px;
}

button {
  margin-top: 30px;
  background-color: #AB3333;
  color: #fff4f4;
  padding: 15px 30px;
  border: none;
  cursor: pointer;
  align-self: center;
  border-radius: 25px;
}

button:hover {
  background-color: #970000;
}